<template>
    <div class="container">
        <PageLoadding v-if="show"></PageLoadding>
    </div>
</template>

<script>
    import PageLoadding from "@/components/PageLoadding.vue";
    import Mixins from './Mixins.js'
    
    export default {
        mixins:[ Mixins ],
        components: {
            PageLoadding
        },
        data(){
            return {
                name:'vg',
                method: 'getVgUrl',
                gameType: ''
            }
        },
        created() {
            this.gameType = this.$route.params.id
        },
        methods: {
            getVgUrl(){
                this.show= true
                let data = {
                    ...this.config,
                }
                if(this.gameType){
                    data.gameType = this.gameType
                }
                this.$api.links.getVgUrl(data)
                .then(res=>{
                    console.log(res);
                    this.show= false
                    if(res.result==1){
                        if(!this.debug){
                            if(typeof(res.gameUrl) != 'object'){
                                window.location.href = res.gameUrl
                            }else{
                                this.$toast('没有返回链接')
                            }
                        }
                    }else{
                        this.$toast(res.description)
                    }
                })
            }
        }
    }
</script>

