<template>
    <div class="container">
        <PageLoadding v-if="show"></PageLoadding>
    </div>
</template>

<script>
    import PageLoadding from "@/components/PageLoadding.vue";
    import Mixins from "./Mixins.js";

    export default {
        mixins: [Mixins],
        components: {
            PageLoadding
        },
        data(){
            return {
                name: 'fg',
                method: 'getFgLogoHallUrl',
                serviceId: ''
            }
        },
        created() {
            if(this.$route.params.id == '0'){
                this.config.KindID = this.$route.params.id;
            }else {
                this.config.KindID = '0'
                this.serviceId = this.$route.params.id;
            }
        },
        methods: {
            getFgLogoHallUrl(){
                this.show= true
                this.$api.links.getFgLogoHallUrl(this.config)
                .then(res=>{
                    console.log(res);
                    this.show = false;
                    if(res.result == 1 ){
                        if(this.serviceId){
                            let data = {
                                token: this.config.token,
                                uid: this.config.uid,
                                serviceId: this.serviceId,
                                return_url: 1
                            }
                            this.$api.links.loginLeyouGame(data)
                            .then(res1=>{
                                if(res1.result ==1 ){
                                    console.log(res1);
                                    if(!this.debug){
                                        window.location.href = res1.game_url
                                    }
                                }else{
                                    this.$toast(res1.description)
                                }
                            })
                        }else{
                            if(!this.debug){
                                window.location.href = res.gameUrl
                            }
                        }
                    }else{
                        this.$toast(res.description)
                    }
                })
            }
        }
    }
</script>

