import request from '@/tool/utils/request.js'

const config = {}

// 开元棋牌
config.getAllAmount = params => request.globalRequest('/sport/getAllAmount.json', 'POST', params, 3)




// 余额转开元棋牌
config.changeAmount = params => request.globalRequest('/chess/changeAmount.json', 'POST', params, 3)

// 余额转VG
config.upToVg = params => request.globalRequest('/chess/v1/upToVg', 'POST', params, 3)
// VG转余额
config.downVg = params => request.globalRequest('/chess/v1/downVg', 'POST', params, 3)

// 显示VG余额
config.getVgAmount = params => request.globalRequest('/chess/v1/getVgAmount', 'POST', params, 3)


// 余额转FG
config.upToFg = params => request.globalRequest('/chess/v1/upToFg', 'POST', params, 3)
// FG转余额
config.downFg = params => request.globalRequest('/chess/v1/downFg', 'POST', params, 3)

// FG棋牌余额
config.getLyAmount = params => request.globalRequest('/chess/v1/getLyAmount', 'POST', params, 3)

// 开元棋牌转余额
config.unChangeAmount = params => request.globalRequest('/chess/unChangeAmount.json', 'POST', params, 3)


// 转账记录
config.getLogUserCoinListBySC = params => request.globalRequest('/member/getLogUserCoinListBySC.json', 'POST', params, 3)

// CP余额
config.getMyBalance = params => request.globalRequest('/junbo/v1/getMyBalance', 'POST', params, 3)

// 上分到CP
config.upToJb = params => request.globalRequest('/junbo/v1/upToJb', 'POST', params, 3)

// 从CP下分
config.downFromJb = params => request.globalRequest('/junbo/v1/downFromJb', 'POST', params, 3)


// 获取游戏列表
config.getGameList = params => request.globalRequest('/game/v1/getGameList', 'POST', params, 3)



export default config