import dayjs from 'dayjs'

export default function FilterStore(Vue) {
    // 小于10
    function Double(val) {
        return val < 10 ? '0' + val : val + ''
    }
    // 银行卡号
    Vue.filter('number', function(data) {
        var str = data
        var s1 = str.substring(0, 6)
        var s2 = str.substring(str.length - 3, str.length)
        return s1 + '****' + s2
    })
     // 投注记录 订单编号
     Vue.filter('orderNumber', function(data) {
        var str = data
        var s1 = str.substring(0, 8)
        var s2 = str.substring(str.length - 6, str.length)
        return s1 + '***' + s2
    })
    // 小数点
    Vue.filter('fixed', function(data, line) {
        let money = data * 1
        return money.toFixed(line || 2)
    })
    // 期号
    Vue.filter('lotteryNumber', (data, length) => {
        // 202005220609
        if (!data) return '000'

        var len = data.length
        var str = data.substring(len - length, len)

        return str
    })
    // 倒计时
    Vue.filter('CountDown', (data, type) => {
        if (data >= 0) {
            var h = Double(Math.floor(data / 60 / 60))

            var m = Double(Math.floor(data / 60))

            var s = Double(data % 60)
            if(type){
                var time = ''.concat(m,'分', s,'秒')
            }else{
                var time = ''.concat(h, ':', m, ':', s);
            }
            return time
        } else {
            return '00:00:00'
        }
    }),
    // 资金记录 类型
    Vue.filter('zijin_type', data => {
        switch (data) {
            case -1:
                return '全部'
            case 2:
                return '提现/用户出款'
            case 3:
                return '充值/用户入款'
            case 9:
                return '彩金/入款优惠'
            case 11:
                return '投注/投注扣款'
            case 12:
                return '派奖/投注派奖'
            case 13:
                return '撤单/投注撤单'
            case 14:
                return '手动扣款'
            case 16:
                return '提现/用户出款(拒绝)'
            case 17:
                return '彩票返水'
            case 18:
                return '彩票返点'
            case 19:
                return '代理佣金'
            case 26:
                return '[开元游戏]转[钱包]'
            case 27:
                return '[钱包]转[开元游戏]'
            case 37:
                return '[FG游戏]转[钱包]'
            case 38:
                return '[钱包]转[FG游戏]'
            case 42:
                return '[钱包]转[VG财神游戏]'
            case 43:
                return '[VG财神游戏]转[钱包]'
            case 12:
                return '派奖/投注派奖'
            case 44:
                return '抢红包'
            case 45:
                return '独立赠送彩金'
            case 46:
                return '晋级彩金'
            case 47:
                return '入款优惠'
            case 48:
                return '月月送钱'
            case 49:
                return '注册送彩金'
            case 54:
                return '直播收益'
            case 55:
                return '直播消费'
            case 56:
                return '[君博彩票]转[钱包]'
            case 57:
                return '[钱包]转[君博彩票]'
            default:
                return data
        }
    })
    // 提现记录 类型
    Vue.filter('tixian_type', data => {
        switch (data) {
            case 1:
                return '银行出款'
            default:
                return data
        }
    })
    // 提现记录 状态
    Vue.filter('tixian_status', data => {
        switch (data) {
            case 0:
                return '出款中...'
            case 1:
                return '出款成功'
            case 2:
                return '提现失败'
            default:
                return data
        }
    })
    // 充值记录 类型
    Vue.filter('chongzhi_type', data => {
        switch (data) {
            case 1:
                return '银行转账'
            case 2:
                return '支付宝转账'
            case 3:
                return '微信转账'
            case 4:
                return '财付通转账'
            case 5:
                return '在线支付'
            case 6:
                return 'QQ转账'
            case 7:
                return '京东转账'
            case 8:
                return '百度钱包转账'
            case 9:
                return '微信转银行卡'
            case 10:
                return '云闪付'
            case 11:
                return '支付宝转银行卡'
            default:
                return data
        }
    })
    // 充值记录 状态
    Vue.filter('chongzhi_status', data => {
        switch (data) {
            case 0:
                return '正在入款'
            case 1:
                return '充值成功'
            case 2:
                return '充值失败'
            default:
                return data
        }
    })
    // 时间戳 转 时间
    Vue.filter('timer', data => {
        if(data){
            return dayjs(data).format('YYYY-MM-DD HH:mm:ss')
        }else{
            return '00:00:00'
        }
    })
    Vue.filter('timer1', data => {
        if(data){
            return dayjs(data).format('YYYY-MM-DD')
        }else{
            return '00:00:00'
        }
    })
}