import dayjs from 'dayjs'
import filter from "./filter";
import assist from './assist.js'

import { mapState } from "vuex";

let plug = {}

plug.install = function(Vue, options) {
    // 指令
    Vue.directive('number', {
        inserted: function(el, binding, vnode) {
            el.focus()
        },
    })

    // 混入
    Vue.mixin({
        data(){
            return {
                env: {}
            }
        },
        watch:{
            beOverdue(user){
                this.$store.commit('User/SET_USER', user);
            }
        },
        created() {
            var env = assist.getUserAgent()
            this.env = env
            if (env.build == 'APP') {
                this.env.app = true
            } else {
                this.env.app = false
            }
        },
        computed: {
            ...mapState('User', {
                baseUser: state => state.baseUser,
                beOverdue: state => state.beOverdue,
            }),
            ...mapState('Game', {
                $me: state => state.getUserBaseInfo,
            })
        },
        methods: {
            // 生成标题
            callHandle(json) {
                if (this.env.isIOS) {
                    window.webkit.messageHandlers.NativeModel.postMessage(json);
                }
                if (this.env.isAndroid) {
                    window.android.NativeModel(json);
                }
            },
            // 银行图标
            bankPicture(name) {
                console.log(name);
                switch (name) {
                    case '北京银行':
                        return '4.png'
                    case '工商银行':
                        return '2.png'
                    case '光大银行':
                        return '5.png'
                    case '广发银行':
                        return '6.png'
                    case '华夏银行':
                        return '7.png'
                    case '建设银行':
                        return '8.png'
                    case '民生银行':
                        return '9.png'
                    case '交通银行':
                        return '3.png'
                    case '宁波银行':
                        return '10.png'
                    case '农业银行':
                        return '11.png'
                    case '平安银行':
                        return '12.png'
                    case '浦发银行':
                        return '13.png'
                    case '上海银行':
                        return '15.png'
                    case '深圳发展银行':
                        return '16.png'
                    case '兴业银行':
                        return '17.png'
                    case '邮政银行':
                        return '18.png'
                    case '招商银行':
                        return '19.png'
                    case '中国银行':
                        return '1.png'
                    case '中信银行':
                        return '20.png'
                    default:
                        return '14.png'
                }
                
            },
            // mui-scroll 区域滚动
            mui_scroll(ele) {
                setTimeout(() => {
                    this.mui(ele).scroll({
                        scrollY: true, //是否竖向滚动
                        scrollX: false, //是否横向滚动
                        startX: 0, //初始化时滚动至x
                        startY: 0, //初始化时滚动至y
                        indicators: true, //是否显示滚动条
                        deceleration: 0.0006, //阻尼系数,系数越小滑动越灵敏
                        bounce: true //是否启用回弹
                    })
                }, 200);
            }
        }
    })

    // 过滤器
    filter(Vue)

    Vue.filter('Date', data => dayjs(data).format('YYYY-MM-DD HH:mm:ss'))

    Vue.prototype.$user = function(methodOptions) {
       console.log('mixins $user function');
       return this.$route.query
    }
    Vue.prototype.$assist = assist
}

export default plug