import $api from '../../api/index.js'

import {
    ACTIVITY,
    ACTIVITY_GET_PROMO_TION
} from "../../common/constants.js";


const state = {
    [ACTIVITY_GET_PROMO_TION]: false
}

const mutations = {
    SET_INDEX_URL(state, payload){
        console.log(payload);
        state[ACTIVITY_GET_PROMO_TION]['promotionList'][payload.index]['url'] = payload.url
    }
}

const actions = {}

console.log('活动模块 => ', ACTIVITY);

ACTIVITY.forEach(item => {
    mutations[item] = (state, payload) => {
        state[item] = payload
    },
    actions[item] = ({commit}, payload) => {
        return new Promise(resolve => {
            setTimeout(() => {
                $api.activity[item](payload)
                .then(res => {
                    commit(item, res)
                    resolve(res)
                })
            }, 200)
        })
    }
})

export default {
    namespaced: true,
    state,
    mutations,
    actions
}