import $api from '../../api/index.js'

import {
    USER,
    getCoinOperEnumList,
    getUserBankCardList
} from "../../common/constants.js";

const state = {
    capial_record_select: false,
    [getCoinOperEnumList]: false,
    [getUserBankCardList]: false,
    baseUser: false,
    beOverdue: false,
    withdraw_curr: false,
}

const mutations = {
    SET_CAPIAL_RECORD(state, payload) {
        state.capial_record_select = payload
    },
    SET_FLASE(state){
        state.capial_record_select = false
    },
    SET_USER(state, payload){
        state.baseUser = payload
    },
    SET_BEOVERDUE(state, payload){
        state.beOverdue = payload
    },
    SET_WITHDRAW(state, payload){
        state.withdraw_curr = payload
    }
}

const actions = {

}

console.log('个人中心模块 => ', USER);

USER.forEach(item => {
    mutations[item] = (state, payload) => {
        state[item] = payload
    },
    actions[item] = ({commit}, payload) => {
        return new Promise(resolve => {
            setTimeout(() => {
                $api.user[item](payload)
                .then(res => {
                    commit(item, res)
                    resolve(res)
                })
            }, 200)
        })
    }
})

export default {
    namespaced: true,
    state,
    mutations,
    actions
}