<template>
    <div class="container">
        <PageLoadding v-if="show"></PageLoadding>
    </div>
</template>

<script>
    import PageLoadding from "@/components/PageLoadding.vue";

    import Mixins from './Mixins.js'

    export default {
        mixins:[ Mixins ],
        components: {
            PageLoadding
        },
        data(){
            return {
                config : {
                    KindID: 0
                },
                name: 'ky',
                method: 'getChessUrl'
            }
        },
        created() {
            this.config.KindID = this.$route.params.id
        },
        methods: {
            getChessUrl(){
                this.show= true
                this.$api.links.getChessUrl(this.config)
                .then(res=>{
                    console.log(res);
                    this.show= false
                    if(res.result==1){
                        if(!this.debug){
                            window.location.href = res.loginUrl
                        }
                    }else{
                        this.$toast(res.description)
                    }
                })
            }
        }
    }
</script>

