<template>
    <div class="container back-one">
        <!-- <Header :back="true" title="支付宝转账银行卡">
            <span slot="right" @click="show2=true" class="jiao">转账教程</span>
        </Header> -->

        <div class="bank-card">
            <div class="tabs">
                <ul>
                    <div class="line" :style="{'left':`${tabs * 50}%`}">
                        <span></span>
                    </div>
                    <li @click="tabs=0" :class="{'acti': tabs==0}">
                        <span>转账信息</span>
                    </li>
                    <li  @click="tabs=1" :class="{'acti': tabs==1}">
                        <span>支付完成提交订单！</span>
                    </li>
                </ul>
            </div>

            <div class="bank-info">
                <div class="wrap" :style="{'transform':`translateX(${-tabs * 7.5}rem)`}">

                    <!-- 第一个界面 -->
                    <div class="slide">
                        <div class="tips colors">支付宝转银行卡<span>{{money}}</span>元到以下账户</div>
                        <ul class="ul0" v-if="bankcardList">
                            <li>
                                <span>收款银行：</span>
                                <strong>{{bankcardList[select]['bankName']}}</strong>
                                <button @click="show1=true">更多账号</button>
                            </li>
                            <li>
                                <span>转账卡号：</span>
                                <strong>{{bankcardList[select]['bankAccount']}}</strong>
                                <button type="button"
                                    v-clipboard:copy="bankcardList[select]['bankAccount']"
                                    v-clipboard:success="onCopy"
                                    >复制</button>
                            </li>
                            <li>
                                <span>开户姓名：</span>
                                <strong>{{bankcardList[select]['userName']}}</strong>
                                <button type="button"
                                    v-clipboard:copy="bankcardList[select]['userName']"
                                    v-clipboard:success="onCopy"
                                    >复制</button>
                            </li>
                            <li>
                                <span>开户网点：</span>
                                <strong>{{bankcardList[select]['subBankName']}}</strong>
                                <button type="button"
                                    v-clipboard:copy="bankcardList[select]['subBankName']"
                                    v-clipboard:success="onCopy"
                                    >复制</button>
                            </li>
                        </ul>
                        <loadding v-else></loadding>

                        <div class="open-app">
                            <button @click="tabs = 1">转账完成，下一步</button>
                        </div>
                    </div>

                    <!-- 第二个界面 -->
                    <div class="slide">
                        <ul class="ul1">
                            <li>
                                <span>充值金额：</span>
                                <input class="red" type="text" readonly v-model="money">
                            </li>
                            <li>
                                <span>入款时间：</span>
                                <input type="text" @click="show=true" readonly v-model="formData.rkTime">
                            </li>
                            <li v-if="dyParam.dyParam">
                                <span>{{dyParam.dyParam}}：</span>
                                <input v-model="formData.unParam" type="text" :placeholder="dyParam.dyParamTip">
                            </li>
                        </ul>
                        <div class="open-app">
                            <button @click="submitFun">提交订单，审核入款！</button>
                        </div>
                    </div>
                </div>
            </div>

            <div class="zhan-btn" @click="scroll">
                <button>转账教程</button>
            </div>
            
            <div class="zhan-btn">
                <button @click="onOpen">打开支付宝</button>
            </div>

            <van-popup
                v-model="show"
                position="bottom"
                :style="{ height: '40%' }"
            >
            <van-datetime-picker
                v-model="currentDate"
                type="datetime"
                title="选择入款时间"
                :min-date="minDate"
                :max-date="maxDate"
                @cancel="show=false"
                @confirm="confirm"
                />
            </van-popup>

            <van-popup
                v-model="show1"
                position="bottom"
                :style="{ height: '50%' }"
            >
            <van-picker
                title="收款银行"
                show-toolbar
                :columns="columns"
                @confirm="onConfirm"
                @cancel="show1=false"
            />
            </van-popup>
        </div>

        <!-- 教程弹窗 -->
        <div class="alert-course" v-if="show2">
            <div class="layout">
                <h2>转账教程<var @click="show2=false">关闭</var></h2>
                <div class="box">
                    <div class="scroll mui-scroll">
                        <p><i>1、</i>打开支付宝“首页”，点击“转账”：</p>
                        <img src="../../static/img/zfb/zfb4.png" alt="">
                        <p><i>2、</i>进入转账页面，点击“转到银行卡”：</p>
                        <img src="../../static/img/zfb/zfb5.png" alt="">
                        <p><i>3、</i>根据提示填写对应信息，提交即可。</p>
                        <img src="../../static/img/zfb/zfb6.png" alt="">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from "vuex";
    import { dataToString, getDay } from "@/tool/common/time.js";
    export default {
        data(){
            return {
                tabs: 0,

                select:0,
                bankcardList: false,

                show:false,

                show1: false,

                show2: false,

                minDate: new Date(2020, 0, 1),
                maxDate: new Date(2025, 10, 1),
                currentDate: new Date(),

                formData:{
                    rkTime:'',
                    unParam:'',
                },

                dyParam:{

                }
            }
        },
        created(){
            this.getNetData();
            this.formData.rkTime = getDay();
            this.getDyParam4AliPay();
        },
        computed:{
            ...mapState('Recharge',{
                user: state => state.user,
                money: state => state.money
            }),
            columns(){
                var arr = []
                if(this.bankcardList){
                    this.bankcardList.forEach(item=>{
                        arr.push(`${item.bankName}-${item.bankAccount}`)
                    })
                }
                return arr
            }
        },
        methods: {
            scroll(){
                this.show2 = true
                this.$nextTick(()=>{
                    this.mui(".alert-course .layout .box").scroll({
                        scrollY: true, //是否竖向滚动
                        scrollX: false, //是否横向滚动
                        startX: 0, //初始化时滚动至x
                        startY: 0, //初始化时滚动至y
                        indicators: true, //是否显示滚动条
                        deceleration: 0.0006, //阻尼系数,系数越小滑动越灵敏
                        bounce: true //是否启用回弹
                    })
                })
            },
            submitFun(){
                this.$toast.loading({
                    duration: 0,
                    forbidClick: true,
                    message: '加载中',
                });
                console.log(this.bankcardList);
                var data = {
                    ...this.user,
                    ...this.formData,
                    money: this.money,
                    bankCardId: this.bankcardList[this.select]['id'],
                }
                this.$api.recharge.submitAliPay2Bank(data)
                .then(res=>{
                    console.log(res);
                    setTimeout(() => {
                        if(res.result==1){
                            this.$toast({message:`已成功提交\r\n等待3-10分钟查看余额`, duration: 3000,})
                            this.$router.back(-1)
                        }else{
                            this.$toast(res.description)
                        }
                    }, 200);
                })
            },
            confirm(date){
                this.show = false
                this.formData.rkTime = dataToString(date)
            },
            onConfirm(value, index){
                this.select = index
                this.show1 = false
            },
            onOpen(){
                this.$dialog.alert({
                    title: '提示',
                    message: '请转账完成后切换至该页面提交订单！',
                    }).then(() => {
                    this.tabs = 1
                    if(this.env.app){
                        var json = 'zfb'
                        if(this.env.isAndroid){
                            window.android.openChat(json);
                        }
                        if(this.env.isIOS){
                            window.webkit.messageHandlers.openChat.postMessage(json);
                        }
                    }else{
                        window.open('alipay://')
                    }
                });
            },
            onCopy(){
                this.$toast('复制成功')
            },
            // 列表
            getNetData(){
                this.$api.recharge.getSystemBankCard(this.user)
                .then(res=>{
                    this.bankcardList = res.bankcardList
                })
            },
            getDyParam4AliPay(){
                this.$api.recharge.getDyParam4AliPay(this.user)
                .then(res=>{
                    console.log(res);
                    if(res.result){
                        this.dyParam = res
                    }
                })
            }
        },

    }
</script>

<style>

</style>