<template>
    <div id="app">
        <router-view />
    </div>
</template>

<script>
    import request from '@/tool/utils/request.js'
    export default {
        data(){
            return {
                date: '',
            }
        },
        created(){
            const _this = this
            console.log('app created');
            _this.fontSize(750);

            // window.addEventListener('touchmove', function(e){
            //     e.preventDefault()
            // }, { passive: false })

            window.onresize = function(){
                _this.fontSize(750)
            }
        },
        mounted(){
            console.log('app mounted');
            const _this = this
            window.resref = function(){
                var date = new Date()
                if(date-_this.date > 1000){
                    _this.date = date;
                }else{
                    return false;
                }
                if(_this.env.app){
                    if(_this.env.isAndroid){
                        // console.log('安卓调用成功');
                        window.android.resetWebview();
                    }
                    if(_this.env.isIOS){
                        window.webkit.messageHandlers.resetWebview.postMessage('');
                    }
                }
            }
        },
        methods: {
            fontSize(devwidth) {
                var deviceWidth = document.documentElement.clientWidth;
                if (deviceWidth >= devwidth) deviceWidth = devwidth;
                document.documentElement.style.fontSize = deviceWidth / (devwidth / 100) + 'px';
            },
        }
    }
</script>

