/* eslint-disable */

import Vue from "vue";

import App from "./App.vue";
import router from "./router";
import $ from 'jquery';

import VueClipboard from 'vue-clipboard2'
Vue.use(VueClipboard)

// 加载插件
import VueScroller from 'vue-scroller'
Vue.use(VueScroller)

import {
    Swipe,
    SwipeItem,
    Lazyload,
    Loading,
    Toast,
    Picker,
    ActionSheet,
    Dialog,
    DatetimePicker,
    Popup
} from 'vant';

Vue.use(Swipe);
Vue.use(SwipeItem);
Vue.use(Lazyload);
Vue.use(Loading);
Vue.use(Toast);
Vue.use(Picker);
Vue.use(ActionSheet);
Vue.use(Dialog);
Vue.use(DatetimePicker);
Vue.use(Popup);

//服务器地址
import url_config from './tool/common/config.js';

// 接口模块
import api from './tool/api';

// vuex
import store from './tool/store';


import './assets/lib/mui/css/mui.css'
import mui from './assets/lib/mui/js/mui.js'
Vue.prototype.mui = mui;

import md5 from 'js-md5';
Vue.prototype.$md5 = md5;

// 自定义组件
import Loadding from "@/components/Loadding.vue";
import SealedDisk from "@/components/SealedDisk.vue";
import ImageLoad from "@/components/ImageLoad.vue";
import Header from "@/components/Header.vue";

// 全局注册组件
Vue.component('loadding', Loadding);
Vue.component('sealed-disk', SealedDisk);
Vue.component('image-load', ImageLoad);
Vue.component('Header', Header);

// 自定义插件
import scroll from "@/tool/plug/scroll";
import mixins from "@/tool/plug/mixins";
import component from "@/tool/plug/component";
Vue.use(scroll)
Vue.use(mixins)
Vue.use(component)

// 全局挂载
Vue.prototype.$api = api;
Vue.prototype.$url = url_config;
Vue.prototype.$store = store;

// debug
console.log('接口地址:', url_config);

// 全局样式
import "./static/css/style.min.css";
import "./static/css/animate.min.css";

Vue.config.productionTip = false;

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount("#app");