<template>
  <div class="container back-one">
    <scroller :on-refresh="onRefresh">
      <div style="background: red;">
        <span class="vipenjoy-banner">
                    <img v-if="bannerImg !='' " :src="bannerImg" width="100%" height="71.5" />
<!--          <img src="../../static/img/Vipenjoy/10025.jpg" height="71.5" />-->
        </span>
        <div class="vipenjoy-userinfo">
          <img src="../../static/img/Vipenjoy/jianglh_03.png" width="100%;" />
          <span class="vipenjoy-username">
            账号名称：
            <span style="color: #fcff00;">{{UserSession.nickname}}</span>
          </span>
          <span class="vipenjoy-jifen">
            我的积分：
            <span style="color: #fcff00;">{{ UserSession.points || "0"}}</span>
          </span>
          <span class="vipenjoy-dengji">
            当前等级：
            <span style="color: #fcff00;">{{UserSession.layerName || "普通会员"}}</span>
          </span>
          <span class="vipenjoy-jiangli">
            晋级奖励：
            <span style="color: #fcff00;">{{canBonus}}</span>
          </span>
        </div>
        <span class="vipenjoy-button-gif" @click="userBouns">
          <img src="../../static/img/Vipenjoy/jjcj_qr.gif" width="50%" />
        </span>
        <span class="vipenjoy-text-img-01">
          <img src="../../static/img/Vipenjoy/jal_11.png" width="70%" />
        </span>
        <span class="vipenjoy-text-title" v-text="`会员每晋升一个等级，都能获得奖励，最高可达 ${moneys} 元。`"></span>
        <div class="vipenjoy-table">
          <table cellpadding="0" cellspacing="0" width="100%" class="youh_table">
            <tbody>
              <tr>
                <th>等级</th>
                <th>成长积分</th>
                <th>晋级奖励(元)</th>
                <th>跳级奖励(元)</th>
              </tr>
              <tr v-for="(item,index) in tableList" :key="index">
                <td>{{item.level}}</td>
                <td>{{item.threshold}}</td>
                <td>{{item.bonus}}</td>
                <td>{{item.tjBonus}}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <span class="vipenjoy-text-img-02">
          <img src="../../static/img/Vipenjoy/jal_14.png" width="70%" />
        </span>
        <div class="vipenjoy-container-texr" v-html="bottomInfo">
          <!-- <span class="vipenjoy-text-01">
                  VIP晋级彩金仅需
                  <span style="letter-spacing: 0px; color: rgb(0, 176, 80);">一倍流水</span>即可提款。
                </span>
          <span class="vipenjoy-text-02">举例：会员已累计100以上有效投注即可升级为，如果再产生400以上有效投注即可升级为VIP2</span>-->
        </div>
        <span class="vipenjoy-footer">{{copyRight || "Copyright © 2009-2020 11086 版权所有"}}</span>
      </div>
    </scroller>
  </div>
</template>

<script>
  import { mapState } from "vuex";
  import serverURL from "@/tool/common/config.js";
  import {
    GAME_GET_USER_BASE_INFO
  } from "@/tool/common/constants.js";

  import { companyShortName } from "@/tool/common/config.js";
  import $ from "jquery";
export default {
  name: "VipEnjoy",
  data() {
    return {
      config: {
        uid: '',
        token:'',
      },
      UserSession: {},
      currentPoint: "",
      companyShortName:companyShortName,
      canBonus: "",
      bannerImg: "",
      tableList: [],
      bottomInfo: "",
      isLoading: false,
      vip1CurrPoint: "",
      currentLever: "",
      moneys: 0,
      copyRight:""
    };
  },
  computed: {
    ...mapState('Game', {
      UserBaseInfo: state => state[GAME_GET_USER_BASE_INFO],
    })
  },
  created() {
    const {uid, token} = this.$route.query;
    this.config.uid = uid;
    this.config.token = token;
    var user = this.$user()
    this.$store.commit('User/SET_USER', user);
    this.getUserBaseInfo();
  },

  mounted() {
    this.showUserPointLevel();
    this.showUserCanBouns();
    this.getPointLevels();
    this.getUserSession();
    this.getWebInfo();
  },
  methods: {
    getUserSession(){
      const _this = this
      this.$api.user.getUserSession(this.baseUser)
              .then(res=>{
                _this.UserSession = res
                _this.vip1CurrPoint = res.points;
              })
    },
    getWebInfo(){
      const _this = this
      this.$api.user.getWebInfo()
              .then(res=>{
                _this.copyRight = res.webDbxx;
              })
    },
    getUserBaseInfo(isref){
      if(isref){
        this.$toast('正在刷新')
      }
      const {uid, token} = this.config
      var data = {
        uid,
        token,
      }
      this.$store.dispatch(`Game/${GAME_GET_USER_BASE_INFO}`, data)
              .then(()=>{
                if(isref){
                  this.$toast('刷新完成')
                  $('.ico-ref').removeClass('acti')
                }
              })
    },

    onRefresh(done) {
      var _this = this;
      setTimeout(() => {
        _this.getUserSession();
        _this.showUserPointLevel();
        _this.showUserCanBouns();
        _this.getPointLevels();
        done();
      }, 1000);
    },
    reutrn() {
      this.$router.go(-1);
    },
    getUserSession(){
      const _this = this
      this.$api.user.getUserSession(this.baseUser)
              .then(res=>{
                _this.UserSession = res
              })
    },
    showUserPointLevel() {
      const _this = this
      this.$api.user.getUserSession(this.baseUser)
              .then(res=>{
                _this.currentLever = res.layerName;
                _this.currentPoint = res.currentPoint || 0;
                _this.vip1CurrPoint = res.currentPoint || 0;
              })
    },
    showUserCanBouns() {
      const _this = this
      this.$api.user.showUserCanBouns(this.baseUser)
              .then(res=>{
                _this.canBonus = res.canBonus || 0;
              })
    },

    userBouns() {
      var _this = this;
      if (
        _this.canBonus === 0 ||
        _this.canBonus === null ||
        _this.canBonus == null
      ) {
        _this.$toast("无晋级奖励金额！");
      } else {
        this.$api.user.userBouns(this.baseUser)
                .then(res=>{
            if (res.result == -1000) {
              _this.mui.confirm(
                      res.description || "为了保障现金顺利提现，请先绑定银行卡。",
                "温馨提示",
                ["取消", "立即绑定"],
                function (e) {
                  if (e.index == 1) {
                    _this.$router.push("/Bankcardadd");
                  }
                }
              );
              return;
            }
            if (res.result == 1) {
              _this.mui.alert("领取成功！", "温馨提示", () => { });
              _this.canBonus = 0;
            }
        });
      }
    },
    // 获取晋级列表
    getPointLevels() {
      var _this = this;
      this.$api.user.getPointLevels()
              .then(res=>{
                if(res.actionImageId == "" || res.actionImageId == null){
                  _this.bannerImg = "";
                }else{
                  _this.bannerImg = serverURL + "/image/" + res.actionImageId + ".png";
                }
        _this.tableList = res.list;
        _this.moneys = _this.tableList[_this.tableList.length - 1].bonus;
        _this.bottomInfo = res.actionInfo;
      });
    }
  }
};
</script>

<style scoped >
