import $api from '../../api/index.js'

import {
    GAME,
    GAME_GET_SSC_PLAY_GROUP,
    GAME_GET_PLAN_OPEN_DATA_HISTORY,
    GAME_GET_SSC_PLAY_PL,
    GAME_GET_SSC_OPEN_TIME2,
    GAME_GET_SSC_PLAY_PL2,
    GAME_GET_BET2,
    GAME_GET_USER_BASE_INFO,
    GAME_GET_ALLSSCOPENTIME,
} from "../../common/constants.js";


const state = {
    [GAME_GET_SSC_PLAY_GROUP]: {
        result: 1,
        id: 14,
        name: false,
        shortName: "xyft",
        kqIsOpen: true,
        gfIsOpen: true,
        playModYxms: 0,
        enable: true,
        openInCompany: true,
        show: true,
        doubleMod: true,
    },
    [GAME_GET_PLAN_OPEN_DATA_HISTORY]: {
        "result": 1,
        "sscHistoryList": [{
            "number": "20200910000",
            "openCode": false,
            "playGroupId": null,
            "openTime": 1600353840000,
        }]
    },
    [GAME_GET_SSC_PLAY_PL]: {},
    [GAME_GET_SSC_OPEN_TIME2]: {
        enable: true,
        isFengpan: 1,
        leftOpenTime: 196,
        leftTime: 166,
        nextOpenTime: 1600354140000,
        number: "20200900000",
        openDataTime: 1600354140000,
        opening: true,
        result: 1,
    },
    [GAME_GET_BET2]: {},
    [GAME_GET_SSC_PLAY_PL2]: false,
    [GAME_GET_USER_BASE_INFO]: false,
    [GAME_GET_ALLSSCOPENTIME]: false,

    'playGroupId':'',
    'chip': 0,
    'sscBetList':[]
}

const mutations = {
    // 设置游戏ID
    SET_PLAY_GROUP_ID(state, payload){
        state.playGroupId = payload
    },
    // 设置筹码
    SET_CHIP(state, payload) {
        state.chip = payload
        state.sscBetList.forEach(item => {
            item.perMoney = payload
            item.totalMoney = payload * item.zhushu
        })
    },
    // 设置注单
    SET_SSC_BET_LIST(state, payload) {
        state.sscBetList = payload
    },
    // 更新期号
    UPDATE_NUMBER(state) {
        state.sscBetList.forEach(item => {
            item.number = state[GAME_GET_SSC_OPEN_TIME2].number
        })
    },
    // 清除注单
    CLEAR_BET_LIST(){
        state.sscBetList = []
    }
}

const actions = {}

console.log('游戏模块 => ', GAME);

GAME.forEach(item => {

    mutations[item] = (state, payload) => {
        state[item] = payload
    },
    actions[item] = ({commit}, payload) => {
        return new Promise(resolve => {
            setTimeout(() => {
                $api.game[item](payload)
                .then(res => {
                    commit(item, res)
                    resolve(res)
                })
            }, 200)
        })
    }
})

export default {
    namespaced: true,
    state,
    mutations,
    actions
}