<template>
    <div class="container back-one">
        <!-- <Header :back="true" title="支付宝转账">
            <span slot="right" @click="show2=true" class="jiao">付款教程</span>
        </Header> -->
        <div class="transfer-page" v-if="skInfoList">
            <div class="di-money">最低限额：<i class="red">{{skInfoList[select]['minMoney']}}</i>元 , 最高限额: <i class="red">{{skInfoList[select]['maxMoney']}}</i>元</div>
            <ul>
                <li @click="show=true">
                    <span>收款信息</span>
                    <div class="txt"></div>
                    <div class="fr">
                        <i class="ico-i1"></i>
                    </div>
                </li>
                <li>
                    <span>支付宝：</span>
                    <div class="txt c1">{{skInfoList[select]['account']}}</div>
                    <div class="fr">
                        <button
                            v-clipboard:copy="skInfoList[select]['account']"
                            v-clipboard:success="onCopy"
                            >复制</button>
                    </div>
                </li>
                <li>
                    <span>收款人：</span>
                    <div class="txt c2">{{skInfoList[select]['username']}}</div>
                    <div class="fr">
                        <button
                            v-clipboard:copy="skInfoList[select]['username']"
                            v-clipboard:success="onCopy"
                            >复制</button>
                    </div>
                </li>
            </ul>
            <div class="weixin-scan">
                <div class="code-pict">
                    <img class="img1" v-if="skInfoList[select].url" :src="skInfoList[select].url" alt="">
                    <img class="img2" v-else src="../../static/img/load.gif" alt="">
                </div>
                <div class="box1">
                    <i class="ico-i1"></i>
                    <p>请使用支付宝扫描二维码完成支付</p>
                </div>
                <div class="link">
                    <a @click.prevent="savePic" href="#">保存二维码</a>
                    <a @click.prevent="openWx" href="#">支付宝转账</a>
                    <a @click.prevent="show2=true" href="#">付款教程</a>
                </div>
            </div>
            <div class="submit">
                <button @click="submitNext">提交</button>
                <p>
                    <router-link to="/bankTransfer" replace>大额充值</router-link>
                    <a href="#">联系客服</a>
                </p>
            </div>
        </div>
        <loadding v-else></loadding>

        <van-popup
            v-model="show"
            position="bottom"
            :style="{ height: '40%' }"
        >
        <van-picker
            title="收款信息"
            show-toolbar
            :columns="columns"
            @confirm="onConfirm"
            @cancel="show=false"
        />
        </van-popup>

        <!-- 教程弹窗 -->
        <div class="alert-course" v-if="show2">
            <div class="layout">
                <h2>付款教程<var @click="show2=false">关闭</var></h2>
                <div class="box">
                    <div class="scroll">
                        <p><i>1、</i>打开支付宝，左上角，点击扫一扫进行转账；</p>
                        <img src="../../static/img/zfb/zfb1.png" alt="">
                        <p><i>2、</i>添加收款人微信号（如：田伟伟），进行微信转账</p>
                        <img src="../../static/img/zfb/zfb2.png" alt="">
                        <p><i>3、</i>点击支付宝下导航，进入朋友页面，点击右上角+，点击添加朋友，输入账号，添加完成后，点击下方+，进行转账；</p>
                        <img src="../../static/img/zfb/zfb3.png" alt="">
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import { mapState } from "vuex";
    export default {
        data(){
            return {
                show: true,
                show2: false,

                select: 0,
                skInfoList: false,

                url:'',
            }
        },
        created(){
            this.getPayOnline3()
        },
        computed:{
            ...mapState('Recharge',{
                user: state => state.user,
            }),
            columns(){
                var arr = []
                if(this.skInfoList){
                    this.skInfoList.forEach(item=>{
                        arr.push(`${item.account}-${item.username}`)
                    })
                }
                return arr
            }
        },
        methods: {
            submitNext(){
                this.$store.commit('Recharge/SET_SELECT', this.skInfoList[this.select])

                this.$router.replace('/zfbConfirm')
            },
            onConfirm(value, index){
                this.select = index
                this.show = false
                this.getImage()
            },
            savePic(){
                this.$toast('长按二维码保存')
            },
            openWx(){
                // window.open('alipay://')
                var json = 'zfb'
                var env = this.$assist.getUserAgent()
                if (env.isAndroid) {
                    window.android.openChat(json);
                }
                if (env.isIOS) {
                    window.webkit.messageHandlers.openChat.postMessage(json);
                }
            },
            onCopy(){
                this.$toast('复制成功')
            },
            getImage(){
                if(!this.skInfoList[this.select].url){
                    var id = this.skInfoList[this.select].imageId
                    this.$api.upload.getImageData({id})
                    .then(res=>{
                        console.log(res);
                        this.$set(this.skInfoList[this.select],'url','data:image/jpeg;base64,' + res.imageData)
                    })
                }
            },
            getPayOnline3(){
                var data = {
                    ...this.user,
                    type: 2,
                }
                this.$api.recharge.getPayOnline3(data)
                .then(res=>{
                    console.log(res.skInfoList);
                    this.skInfoList = res.skInfoList
                    this.getImage()
                })
            }
        },
        
    }
</script>

<style>

</style>