


export default {
    data(){
        return {
            show: false,
            config : {},
            debug: false,
        }
    },
    created(){
        this.config = this.$route.query
        this.query();
    },
    methods: {
        query(){
            var data = {
                uid: this.config.uid,
                token: this.config.token,
                name: this.name,
            }
            this.$api.game.getMzConfig(data)
            .then(res=>{
                console.log(res);
                if(res.data == '1'){
                    // 直接跳转
                    this[this.method]();
                }else{
                    // 授权
                    this.$dialog.confirm({
                        title: '免转授权提示',
                        message: '将金额带到游戏中去',
                    })
                    .then(() => {
                        this.save(1);
                    })
                    .catch(() => {
                        this.save(0);
                    });
                }
            })
        },

        save(val){
            console.log('-------------------');
            var data = {
                uid: this.config.uid,
                token: this.config.token,
                name: this.name,
                state: val,
            }
            this.$api.game.saveMzConfig(data)
            .then(res=>{
                this[this.method]();
            })
        },
    }
}