<template>
    <div>
        <div class="container1">
            <div class="result">
                <p>uid: {{config.uid  || '未传入'}}</p>
                <p>token: {{config.token || '未传入'}}</p>
                <p>companyShortName: {{config.companyShortName || '未传入'}}</p>
                <h2>结果：<span :class="{green:result.result==1,red:result.result!=1}">{{result.result==1 ? '在线': result.description}}</span></h2>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            config: {
                uid:'',
                token:''
            },
            result:{}
        }
    },
    created(){
        const {uid, token} = this.$route.query
        this.config.uid  = uid
        this.config.token  = token

        this.net()
    },
    methods:{
        net(){
            this.$api.game.checkOnline(this.config)
            .then(res=>{
                console.log(res);
                this.result = res
            })
        }
    }
}
</script>

<style>
    .container1{
        padding: .6rem .3rem;
    }
    .container1 h2{
        font-size: .35rem;
    }
    .container1 p{
        margin-bottom: .3rem;
    }
    .container1 h2 .red{
        color: #f00;
    }
    .container1 h2 .green{
        color: #0f0;
    }
</style>