<template>
    <div class="container back-one">
        <!-- <Header :back="true">微信转账</Header> -->
        <div class="wx-confirm-page">
            <div class="item">
                <span>充值金额</span>
                <b>{{money}}</b>
                <var>元</var>
            </div>
            <div class="item1">
                <p>最低限额：<i>{{select.minMoney}}</i>元，最高限额：<i>{{select.maxMoney}}</i>元</p>
            </div>
            <div class="item">
                <span>转账信息</span>
                <input maxlength="10" v-model="formData.userWeixinName" type="text" placeholder="微信名字或转账单号的后四位">
            </div>
            <div class="btn">
                <button :class="{'active': formData.userWeixinName}" @click="submitFun">确认提交，极速入款</button>
            </div>
            <div class="text">
                <p>"如何查询订单号？"</p>
                <span>1、打开微信，点击右下角"我"。</span>
                <span>2、进入"钱包",点击右上角</span>
                <span>3、点击"交易记录"</span>
                <span>4、点击对应的转账信息即可查询转账单号</span>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from "vuex";
    export default {
        data(){
            return {
                formData: {
                    userWeixinName:''
                }
            }
        },
        created(){

        },
        computed: {
            ...mapState('Recharge', {
                user: state => state.user,
                money: state => state.money,
                select: state => state.select
            })
        },
        methods: {
            submitFun(){
                this.$toast.loading({
                    duration: 0,
                    forbidClick: true,
                    message: '加载中',
                });

                var data = {
                    ...this.formData,
                    ...this.user,
                    money: this.money,
                    weixinId: this.select.id,
                }
                this.$api.recharge.submitWeixinzz(data)
                .then(res=>{
                    setTimeout(() => {
                        this.$toast(res.description)
                        if(res.result==1){
                            this.$router.back(-1)
                        }
                    }, 500);
                })
            }
        }
    }
</script>

