
import Binput from "./A/Binput";
import Berror from "./A/Berror";
import Bscroller from "./A/Bscroller";

let plug = {}

plug.install = function(Vue, options) {

    // 注册全局组件
        Vue.component(Binput.name, Binput)
        Vue.component(Berror.name, Berror)
        Vue.component(Bscroller.name, Bscroller)


    // 开奖号码 组件注册

        // 通用
        Vue.component('OpenCode-15', (resolve) => require(['@/views/games/components/OpenCode/O15.vue'], resolve))

        // PK10系列
        Vue.component('OpenCode-14', (resolve) => require(['@/views/games/components/OpenCode/O14.vue'], resolve))
        Vue.component('OpenCode-9', (resolve) => require(['@/views/games/components/OpenCode/O14.vue'], resolve))
        Vue.component('OpenCode-23', (resolve) => require(['@/views/games/components/OpenCode/O23.vue'], resolve))

        // 快三
        Vue.component('OpenCode-18', (resolve) => require(['@/views/games/components/OpenCode/O27.vue'], resolve))
        Vue.component('OpenCode-19', (resolve) => require(['@/views/games/components/OpenCode/O27.vue'], resolve))
        Vue.component('OpenCode-20', (resolve) => require(['@/views/games/components/OpenCode/O27.vue'], resolve))
        Vue.component('OpenCode-27', (resolve) => require(['@/views/games/components/OpenCode/O27.vue'], resolve))


        // 六合彩
        Vue.component('OpenCode-6', (resolve) => require(['@/views/games/components/OpenCode/O29.vue'], resolve))
        Vue.component('OpenCode-29', (resolve) => require(['@/views/games/components/OpenCode/O29.vue'], resolve))
        Vue.component('OpenCode-22', (resolve) => require(['@/views/games/components/OpenCode/O29.vue'], resolve))
        Vue.component('OpenCode-30', (resolve) => require(['@/views/games/components/OpenCode/O29.vue'], resolve))
        Vue.component('OpenCode-31', (resolve) => require(['@/views/games/components/OpenCode/O29.vue'], resolve))
        Vue.component('OpenCode-32', (resolve) => require(['@/views/games/components/OpenCode/O29.vue'], resolve))
}

export default plug