import serverUrl, {
    companyShortName
} from '../common/config.js'

const request = {}

    // application/x-www-form-urlencoded
function contentType(data){
    console.log(data);
    var arr = []
    for (var item in data) {
        var str = `${item}=${data[item]}`
        arr.push(str)
    }
    return arr.join('&')
}

request.globalRequest = (url, method, json = {}, type, power) => {
    var init = {
        companyShortName: companyShortName
    }
    var data = {
        ...init,
        ...json
    }
    
    var murl = url;

    if(type==3){
        murl = url  + '?' + contentType(data);
    }

    const headers = { }

    let form = new FormData()

    for (var item in data){
        form.append(item, data[item])
    }

    let formData = null
    
    switch (type) {
        case 1:
            formData = form
            break;
        case 2:
            formData = JSON.stringify(data)
            headers['content-type'] = 'application/json;charset=UTF-8'
            break;
        default:
            formData = JSON.stringify({})
            break;
    }
    if (power){
        console.log('权限');
        headers['Authorization'] = 'Token xxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx'
    }
    const machiningData = {
        body: formData,
        headers,
        mode: 'cors',
        cancelToken: formData.token,
        redirect: 'follow',
        referrer: 'no-referrer',
    }
    if (method=='POST') machiningData.method = 'POST'
    return new Promise(function(resove, reject) {
        fetch(serverUrl.concat(murl), machiningData)
            .then(function(response) {
                return response.json();
            })
            .then(function(res) {
                if(res.result==108){
                    window.resref()
                }
                resove(res)
            })
            .catch(error => {
                console.error(error)
                reject(error)
            })
    })
}

export default request

export {
    companyShortName
}