import $api from '../../api/index.js'

import {
    RECORD,
    RECORD_GET_SSC_BETS_LIST
} from "../../common/constants.js";


const state = {
    [RECORD_GET_SSC_BETS_LIST]: false
}

const mutations = {
    SET_DATA(state, payload){
        state[RECORD_GET_SSC_BETS_LIST] = payload
    }
}

const actions = {

}

console.log('投注记录模块 => ', RECORD);

RECORD.forEach(item => {
    mutations[item] = (state, payload) => {
        state[item] = payload
    },
    actions[item] = ({commit}, payload) => {
        return new Promise(resolve => {
            setTimeout(() => {
                $api.record[item](payload)
                .then(res => {
                    commit(item, res)
                    resolve(res)
                })
            }, 200)
        })
    }
})

export default {
    namespaced: true,
    state,
    mutations,
    actions
}