<template>
    <div class="container back-one">
        <!-- <Header :back="true">银行汇款</Header> -->
        <div class="tabs">
            <ul>
                <div class="line" :style="{'left':`${tabs * 50}%`}">
                    <span></span>
                </div>
                <li @click="tabs=0" :class="{'acti': tabs==0}">
                    <span>转账信息</span>
                </li>
                <li  @click="tabs=1" :class="{'acti': tabs==1}">
                    <span>支付完成提交订单！</span>
                </li>
            </ul>
        </div>
        <div class="bank-page" v-if="bankcardList">
            <div class="mui-scroll">
                <div class="slide-wrapper"  :style="{'transform':`translateX(${-tabs * 7.5}rem)`}">
                    <div class="item">
                        <h2 class="colors">请汇款<span>{{money}}</span>元到以下账户</h2>
                        <ul>
                            <li @click="show=true">
                                <span>{{bankcardList[select]['bankName']}}</span>
                                <div class="fr">
                                    <i class="ico-i1"></i>
                                </div>
                            </li>
                            <li>
                                <span>开户银行</span>
                                <div class="fr">
                                    <var>{{bankcardList[select]['bankName']}}（{{bankcardList[select]['subBankName']}}）</var>
                                    <button
                                        v-clipboard:copy="bankcardList[select]['bankName']+'（'+ bankcardList[select]['subBankName']+'）'"
                                        v-clipboard:success="onCopy"
                                        >复制</button>
                                </div>
                            </li>
                            <li v-if="false">
                                <span>开户网点</span>
                                <div class="fr">
                                    <var>{{bankcardList[select]['subBankName']}}</var>
                                    <button
                                        v-clipboard:copy="bankcardList[select]['subBankName']"
                                        v-clipboard:success="onCopy"
                                    >复制</button>
                                </div>
                            </li>
                            <li>
                                <span>开户姓名</span>
                                <div class="fr">
                                    <var>{{bankcardList[select]['userName']}}</var>
                                    <button
                                        v-clipboard:copy="bankcardList[select]['userName']"
                                        v-clipboard:success="onCopy"
                                    >复制</button>
                                </div>
                            </li>
                            <li>
                                <span>开户卡号</span>
                                <div class="fr">
                                    <var>{{bankcardList[select]['bankAccount']}}</var>
                                    <button
                                        v-clipboard:copy="bankcardList[select]['bankAccount']"
                                        v-clipboard:success="onCopy"
                                    >复制</button>
                                </div>
                            </li>
                        </ul>
                        <div class="btn">
                            <button @click="tabs=1">汇款完成，进入下一步</button>
                        </div>
                    </div>
                    <div class="item">
                        <h2 class="colors">请填写您的汇款信息</h2>
                        <ul>
                            <li @click="show1=true">
                                <span>{{select1 == -1 ? '请选择您的转出银行' : bankList[select1]['name']}}</span>
                                <div class="fr">
                                    <i class="ico-i1"></i>
                                </div>
                            </li>
                            <!-- <li @click="show2=true">
                                <span>{{select2 == -1 ? '请选择存款方式' : depositChannelList[select2]['name']}}</span>
                                <div class="fr">
                                    <i class="ico-i1"></i>
                                </div>
                            </li> -->
                            <li>
                                <strong>{{money}}</strong>
                            </li>
                            <!-- <li>
                                <input v-model="formData.bankAccount" type="number" pattern="[0-9]*" oninput="if(value.length>4)value=value.slice(0,4)" placeholder="">
                            </li> -->
                            <li>
                                <input v-model="formData.name" type="text" maxlength="10" placeholder="请输入您的汇款姓名">
                            </li>
                        </ul>
                        <!-- <h2>汇款人</h2>
                        <ul>
                            <li>
                                <input v-model="formData.name" type="text" maxlength="10" placeholder="请输入您的汇款姓名">
                            </li>
                        </ul> -->
                        <div class="btn">
                            <button @click="submitYhzz">填写完成，立即提交</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <loadding v-else></loadding>

        <!-- 收款信息 -->
        <van-popup
            v-model="show"
            position="bottom"
            :style="{ height: '40%' }"
        >
        <van-picker
            title="收款信息"
            show-toolbar
            :columns="columns"
            @confirm="onConfirm"
            @cancel="show=false"
        />
        </van-popup>

        <!-- 转出银行 -->
        <van-popup
            v-model="show1"
            position="bottom"
            :style="{ height: '40%' }"
        >
        <van-picker
            title="转出银行"
            show-toolbar
            :columns="bankList_filter"
            @confirm="onConfirm1"
            @cancel="show1=false"
        />
        </van-popup>

        <!-- 存款银行 -->
        <van-popup
            v-model="show2"
            position="bottom"
            :style="{ height: '40%' }"
        >
        <van-picker
            title="存款银行"
            show-toolbar
            :columns="depositChannelList_filter"
            @confirm="onConfirm2"
            @cancel="show2=false"
        />
        </van-popup>
    </div>
</template>

<script>
    import { mapState } from "vuex";
    export default {
        data(){
            return {
                tabs:0,
                show: false,
                select: 0,
                bankcardList: false,

                show1: false,
                select1: -1,
                bankList: false,

                show2: false,
                select2: -1,
                depositChannelList: [],

                formData: {
                    name: '',
                    bankAccount:''
                }
            }
        },
        watch: {
            new_name(new_value){
                var reg = /^[\u4e00-\u9fa5]+$/;
                var arr = new_value.split('')
                var new_arr = []
                arr.forEach(item=>{
                    if(reg.test(item) || item=='·'){
                        new_arr.push(item)
                    }
                })
                this.formData.name = new_arr.join('')
            },
            tabs(n){
                if(n==1){
                    this.show1 = true
                }else{
                    this.show1 = false
                }
            }
        },
        computed: {
            new_name(){
                return this.formData.name
            },
            ...mapState('Recharge', {
                user: state => state.user,
                money: state => state.money
            }),
            columns(){
                var arr = []
                if(this.bankcardList){
                    this.bankcardList.forEach(item=>{
                        arr.push(`${item.bankName}-${item.bankAccount}`)
                    })
                }
                return arr
            },
            bankList_filter(){
                var arr=[];
                if(this.bankList){
                    this.bankList.forEach(item=>{
                        arr.push(item.name)
                    })
                }
                return  arr
            },
            depositChannelList_filter(){
                var arr=[];
                if(this.depositChannelList){
                    this.depositChannelList.forEach(item=>{
                        arr.push(item.name)
                    })
                }
                return  arr
            },
        },
        created(){
            this.getSystemBankCard()
            this.getDepositChannel()
            this.getEnums()
        },
        mounted(){

        },
        methods: {
            onConfirm(value, index){
                this.select = index
                this.show = false
            },
            onConfirm1(value, index){
                this.select1 = index
                this.show1 = false
            },
            onConfirm2(value, index){
                this.select2 = index
                this.show2 = false
            },
            onCopy(){
                this.$toast('复制成功')
            },
            getSystemBankCard(){
                this.$api.recharge.getSystemBankCard(this.user)
                .then(res=>{
                    console.log(res);
                    this.bankcardList = res.bankcardList
                    setTimeout(() => {
                        this.mui(".bank-page").scroll({
                            deceleration:0.0005,
                            indicators: false,
                        })
                    }, 200);
                })
            },
            // 转出银行
            getEnums(){
                this.$api.recharge.getEnums()
                .then(res=>{
                    this.bankList = res.bankList
                })
            },
            // 转出银行
            getDepositChannel(){
                this.$api.recharge.getDepositChannel()
                .then(res=>{
                    this.depositChannelList = res.depositChannelList
                })
            },
            // 提交申请
            submitYhzz(){

                if(this.formData.name == ''){
                    this.$toast('请输入汇款人姓名');
                    return false;
                }

                this.$toast.loading({
                    duration: 0,
                    forbidClick: true,
                    message: '加载中',
                });

                var data = {
                    ...this.formData,
                    ...this.user,
                    money: this.money,
                    // depositChannelId: this.select2 != -1 ?this.depositChannelList[this.select2]['id'] : '',
                    depositChannelId: this.depositChannelList[0]['id'],
                    bankCardId: this.bankcardList[this.select]['id'],
                    userBankName: this.select1 != -1 ? this.bankList[this.select1]['name'] : ''
                }
                console.log(data);
                this.$api.recharge.submitYhzz(data)
                .then(res=>{
                    setTimeout(() => {
                        if(res.result==1){
                            this.$toast({message:`已成功提交\r\n等待3-10分钟查看余额`, duration: 3000,})
                            this.$router.push('/rechargeRecord?uid='+ data.uid +'&token=' + data.token);
                        }else{
                            this.$toast(res.description)

                        }
                    }, 200);
                })
            }
        },

    }
</script>

<style>

</style>
