import $api from '../../api/index.js'

import {
    RECHARGE,
} from "../../common/constants.js";


const state = {
    money: 0,
    user: {},
    select:{}
}

const mutations = {
    // 设置充值金额
    SET_MONEY(state, payload){
        state.money = payload
    },
    // 设置账户
    SET_USER(state, payload){
        state.user = payload
    },
    // 设置最低和最高限额
    SET_SELECT(state, payload){
        state.select = payload
    }
}

const actions = {}

console.log('充值模块 => ', RECHARGE);

RECHARGE.forEach(item => {
    mutations[item] = (state, payload) => {
            state[item] = payload
        },
        actions[item] = ({
            commit
        }, payload) => {
            return new Promise(resolve => {
                setTimeout(() => {
                    $api.recharge[item](payload)
                        .then(res => {
                            commit(item, res)
                            resolve(res)
                        })
                }, 200)
            })
        }
})

export default {
    namespaced: true,
    state,
    mutations,
    actions
}